import http from '@/http';
import CONFIG from '@/assets/js/config';

// 
export function getCopyEditListInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    return http.getAjax('/copywriters/copywriters/getSpuCopywritersList', data);

}

// 
export function confirmBatchUploadInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/supplier/supplier/sup_data_upload', data);
}

// 
export function changeSupplierState (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/supplier/supplier/handle_sup_status', data);
}

// 
export function saveEditInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/supplier/supplier/edit_supplier', data);
}

// 
export function assignSpu (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/copywriters/copywriters/assigned_spu', data);
}

// 
export function reAssignSpu (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.postAjax('/copywriters/copywriters/againDistribution', data);
}

//
export function getRejectInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.getAjax('/copywriters/copywriters/getRejectDown', data);
}

/**
 * :
 *  @file 
 */
export function upLoadPhoto (params) {
    return http.fileAjax('/copywriters/copywriters/uploadImage', params);
}

export function rejectSubmit (params) {
    return http.fileAjax('/copywriters/copywriters/copywritersReject', params);
}



