import http from '@/http';
import CONFIG from '@/assets/js/config';
import state from '@/store/state';

/**
 * :
 *  @id
 *  @mod_type 
 *
 *    1;
 *    2;
 *   3;
 *    4;
 *    5;
 * **/
export function getLogList (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.getAjax('/sample_config/log/log_list', params);
}

/**
 * :
 *  @file 
 */
export function upLoadPhoto (params) {
    return http.fileAjax('/sample_config/sample_config/image_upload/file', params);
}

/**
 * :
 *
 */
export function getUserByFunction (queryData = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...queryData
    };

    if (data.function) {
        if (!state.operateUserList[data.function]) {
            state.operateUserList[data.function] = http.getAjax('/sample_control/sample_check_record/get_user_by_dep', data);
        }
        console.log(state.operateUserList[data.function], 9988);
        return state.operateUserList[data.function];
    } else {
        return http.getAjax('/sample_control/sample_check_record/get_user_by_dep', data);
    }
}

/**
 * :
 *
 */
export function getStatusByFunction (params) {
    switch (params) {
        case 9:
            return {
                '': 11,
                '': 12
            };
        case 1:
            return {
                '': 1
            };
        case 3:
            return {
                '': 3,
                '': 4,
                '': 13
            };
        case 2:
            return {
                '': 2,
                '': 13
            };
        case 5:
            return {
                '': 7,
                '': 8,
                '': 13,
            };
        case 4:
            return {
                '': 5,
                '': 6,
                '': 13,
            };
        default:
            return {
                '': 9,
                '': 10,
                '': 13,
            };
    }
}

/**
 * 
 * @html (string) 
 * */
export function printHtml (params = {}) {
    const data = {
        // ...CONFIG.ajaxData,
        ...params
    };
    return new Promise(resolve => {
        fetch('http://192.168.31.6:9130/print/printHtml', {
            method: 'POST',
            mode: 'no-cors',
            headers: new Headers({
                'Content-Type': 'application/x-www-form-urlencoded' // 
            }),
            body: new URLSearchParams(Object.entries(data)).toString()
        }).then((res) => {
            resolve(1);
        }).catch((e) => {
        });
    });
}



